import React, { useState } from 'react';
import axios from 'axios';
import { sha256 } from 'crypto-js';

const PaymentForm = () => {
  const [formData, setFormData] = useState({
    merchantTransactionId: '',
    merchantUserId: '',
    amount: '',
    mobileNumber: '',
    email: '',
  });

  const [paymentUrl, setPaymentUrl] = useState('');

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Replace with your actual PhonePe API credentials
    const apiKey = '945d1e02-2cb6-437c-84bb-20a27e8554c1';
    const merchantId = 'M22QP4J1NPVS8';
    const keyIndex = 1;

    // Prepare the payment request data
    const paymentData = {
      merchantId,
      merchantTransactionId: formData.merchantTransactionId,
      merchantUserId: formData.merchantUserId,
      amount: formData.amount * 100, // Amount in paisa
      redirectUrl: 'https://cyberspacedigital.in/paymentsuccess.php', // Replace with your redirect URL
      redirectMode: 'POST', // Adjust as per your integration needs
      callbackUrl: 'https://cyberspacedigital.in/paymentsuccess.php', // Replace with your callback URL
      merchantOrderId: 'Test001', // Replace with your unique order ID
      mobileNumber: formData.mobileNumber,
      message: 'Order description', // Replace with order description
      email: formData.email,
      shortName: 'Akhil', // Replace with customer's short name
      paymentInstrument: {
        type: 'PAY_PAGE',
      },
    };

    try {
      // Make POST request to PhonePe API
      const response = await axios.post(
        'https://api.phonepe.com/apis/hermes/pg/v1/pay',
        paymentData,
        {
          headers: {
            'Content-Type': 'application/json',
            'X-VERIFY': `${sha256(JSON.stringify(paymentData))}###${keyIndex}`,
            Accept: 'application/json',
          },
        }
      );

      // Check if request was successful
      if (response.data.success === true) {
        // Extract payment URL from response
        const payUrl = response.data.data.instrumentResponse.redirectInfo.url;
        setPaymentUrl(payUrl); // Set payment URL to state
      } else {
        // Handle payment initiation error
        console.error('Error in Payment:', response.data.message);
      }
    } catch (error) {
      // Handle network errors or API request errors
      console.error('Error in API request:', error);
    }
  };

  return (
    <div className='mt-20'>
      <h2>Enter Payment Details</h2>
      <form onSubmit={handleSubmit}>
        <label>
          Transaction ID:
          <input
            type="text"
            name="merchantTransactionId"
            value={formData.merchantTransactionId}
            onChange={handleChange}
          />
        </label>
        <br />
        <label>
          User ID:
          <input
            type="text"
            name="merchantUserId"
            value={formData.merchantUserId}
            onChange={handleChange}
          />
        </label>
        <br />
        <label>
          Amount (INR):
          <input
            type="number"
            name="amount"
            value={formData.amount}
            onChange={handleChange}
          />
        </label>
        <br />
        <label>
          Mobile Number:
          <input
            type="text"
            name="mobileNumber"
            value={formData.mobileNumber}
            onChange={handleChange}
          />
        </label>
        <br />
        <label>
          Email:
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
          />
        </label>
        <br />
        <button type="submit">Pay with PhonePe</button>
      </form>

      {paymentUrl && (
        <iframe
          title="PhonePe Payment"
          src={paymentUrl}
          style={{ width: '100%', height: '600px', border: 'none' }}
        />
      )}
    </div>
  );
};

export default PaymentForm;
